.content-body {
    .container {
        margin-top: 40px;
    }
    .container-fluid {
        padding-top: 20px;
        padding-right: 40px;
        padding-left: 40px;
		@include custommq($max: 1200px) {
			padding-top: 30px;
			padding-right: 30px;
			padding-left: 30px;
		}
		@include custommq($max: 767px) {
			padding-top: 20px;
			padding-right: 20px;
			padding-left: 20px;
		}
		@include custommq($max: 575px) {
			padding-top: 15px;
			padding-right: 15px;
			padding-left: 15px;
		}
    }
}

